/**
 * 
 * @returns {Array}
 */
function gainMenu(userCat,allMenu){

    let menuItem = [];
    if(userCat.trim()!=="" && !isArrayEmpty(allMenu)){
        if(userCat == '1024'){
            // 
            menuItem = allMenu;
        }else if (userCat == 1){
            // 
            menuItem = allMenu.filter(item => item.ascription.indexOf("") != -1);
        }else if(userCat == 3){
            // 
            menuItem = allMenu.filter(item => item.ascription.indexOf("") != -1);
        }else if(userCat == 9){
            // 
            menuItem = allMenu.filter(item => item.ascription.indexOf("") != -1);
        }else if(userCat == 1129){
            // 
            menuItem = allMenu.filter(item => item.ascription.indexOf("") != -1);
        }else{
            // other ?
        }
    }
    return menuItem;
}


